import { 
    USER_LOGIN_SUCCESS, 
    USER_LOGIN_REQUEST, 
    USER_LOGIN_FAIL, 
    USER_LOGOUT, 
    USER_REGISTER_SUCCESS, 
    USER_REGISTER_FAIL, 
    USER_REGISTER_REQUEST } from "../constants/userConstants";
import axios from 'axios';



export const login = (userID, password) => async (dispatch) => {
    try {
        dispatch({ type: USER_LOGIN_REQUEST });
       
        const url = "http://localhost:8080/authenticate/";
        const config = {
          auth: {
            username: userID,
            password, //this should not be saved in your local storage delete this from here
          },
        };
    
        const data = {};
        const response = await axios.post(
            url, 
            data, 
            config,
        )

        dispatch({ type: USER_LOGIN_SUCCESS, payload: config});
 
        if (response.status === 200) {
          // Login succeeded
          config.token = response.data.token;
          
        }
        localStorage.removeItem("userInfo",JSON.stringify(config.password));
        localStorage.setItem("userInfo", JSON.stringify(config) );
      } catch (error) {
        //alert("Sorry, login failed");
        dispatch({
            type: USER_LOGIN_FAIL,
            payload:
                error.response && error.response.data.ErrorMessage
                    ? error.response.data.ErrorMessage
                    : error.message,
        });
      }
};

export const logout = () => async (dispatch) => {
    localStorage.removeItem("userInfo");
    dispatch({ type: USER_LOGOUT });
  };

export const register =(userID, userName, password) => async (dispatch) => {
   try {
      dispatch({ type: USER_REGISTER_REQUEST});
      
       const config = {
         headers: {
           "Content-type": "application/json",
         }
       };
       const { data } = await axios.post('http://localhost:8080/user/', 
       {
         userID,
         userName,
         password
       },
       config
       );
       console.log(data);

       dispatch({ type: USER_REGISTER_SUCCESS, payload: config});
       
       dispatch({ type: USER_LOGIN_SUCCESS, payload: config});

       localStorage.setItem("userInfo", JSON.stringify(data));
        
    } catch (error) {
        dispatch({
            type: USER_REGISTER_FAIL,
            payload:
                error.response && error.response.data.message
                    ? error.response.data.message
                    : error.message,
        });
       //setError(error.response.data.message);
       console.log(error);
        
      }
  }